.primary-nav {
  transition-property: opacity, visibility;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  visibility: hidden;
  width: 100%;
  z-index: 10;
}

.primary-nav.active {
  opacity: 1;
  visibility: visible;
}

.primary-nav.active li {
  background-color: transparent;
  transform: none;
  opacity: 1;
}

.primary-nav.active .overlay {
  height: 100%;
}

.primary-nav .overlay {
  transition-property: height;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  background-color: rgba(22, 21, 20, 0.9);
  display: block;
  cursor: pointer;
  height: 0;
  right: 0;
  position: absolute;
  top: 0;
  width: inherit;
}

.primary-nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  zoom: 1;
  /* For IE 6/7 (trigger hasLayout) */
  margin-top: 70px;
  overflow-y: scroll;
  padding: 20px 5px;
  position: relative;
  text-align: center;
}

.primary-nav ul:before, .primary-nav ul:after {
  content: '';
  display: table;
}

.primary-nav ul:after {
  clear: both;
}

@media screen and (min-width: 768px), print {
  .primary-nav ul {
    transform: translate(-50%, -50%);
    left: 50%;
    margin: auto;
    position: absolute;
    top: 50%;
    width: 50%;
    height: auto !important;
    margin: auto;
    margin-top: 0;
    max-width: 640px;
    overflow: hidden;
    width: 100%;
    z-index: 10;
  }
}

.primary-nav ul:before {
  background: transparent;
  background-repeat: repeat-x;
  background: linear-gradient(to bottom, rgba(22, 21, 20, 0.9) 0%, transparent 100%);
  height: 20px;
  left: 0;
  position: fixed;
  top: 70px;
  width: 100%;
  z-index: 10;
}

@media screen and (min-width: 768px), print {
  .primary-nav ul:before {
    display: none;
  }
}

.primary-nav li {
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  background-color: #ffffff;
  transform: translate(5px, 20px) scale(0.5);
  will-change: transform;
  float: left;
  height: 40vw;
  opacity: 0;
  margin-bottom: 30px;
  padding: 0 15px;
  position: relative;
  width: 50%;
  z-index: 1;
}

@media (max-width: 767px) and (orientation: landscape) {
  .primary-nav li {
    height: 27vw;
    width: 33.333333%;
  }
}

.primary-nav li:nth-child(1) {
  transition-delay: 0.1s;
}

.primary-nav li:nth-child(1), .primary-nav li:nth-child(4) {
  transition-delay: 0.2s;
}

.primary-nav li:nth-child(3), .primary-nav li:nth-child(6) {
  transition-delay: 0.3s;
}

.primary-nav li:nth-child(5) {
  transition-delay: 0.4s;
}

